import { CloseIcon, HamburgerIcon } from "@chakra-ui/icons"
import { Box, Button, Flex, HStack, IconButton } from "@chakra-ui/react"
import { Link } from "gatsby"
import React, { useState } from "react"

const NavBar = () => {
  const [display, setDisplay] = useState("none")
  return (
    <Box
      as="nav"
      pos="sticky"
      top="0"
      right="0"
      left="0"
      boxShadow="md"
      bg="#fafafa"
      zIndex="10"
      color="#222"
    >
      <Flex justify="space-between" alignItems="center">
        <Flex
          ml={{
            base: "20vw",
            sm: "20vw",
            md: "20vw",
            lg: "20vw",
            xl: "20vw",
            "2xl": "20vw",
            "3xl": "20vw",
            "4xl": "20vw",
          }}
        >
          <Link to="/">
            <Box my={4}>
              <img
                style={{ width: "30px", height: "auto" }}
                src={"/logo_white.png"}
                alt="logo"
              />
            </Box>
          </Link>
        </Flex>
        <Flex
          mr={{
            base: "20vw",
            sm: "20vw",
            md: "20vw",
            lg: "20vw",
            xl: "20vw",
            "2xl": "20vw",
            "3xl": "20vw",
            "4xl": "20vw",
          }}
          my={4}
        >
          <HStack
            as="div"
            display={{
              base: "none",
              sm: "none",
              md: "flex",
              lg: "flex",
              xl: "flex",
            }}
          >
            <Link to="#projects">
              <Button
                as="a"
                variant="outline"
                color="black"
                aria-label="projects"
                fontFamily="Fira"
                fontWeight="700"
              >
                {"projects()"}
              </Button>
            </Link>
            <Link>
              <Button
                as="a"
                variant="outline"
                color="black"
                fontWeight="700"
                aria-label="Blog"
                fontFamily="Fira"
              >
                {"blog()"}
              </Button>
            </Link>
            <Link to="#contact">
              <Button
                as="a"
                variant="outline"
                color="black"
                aria-label="projects"
                fontFamily="Fira"
                fontWeight="700"
              >
                {"contact()"}
              </Button>
            </Link>
          </HStack>
          <IconButton
            aria-label="open menu"
            size="lg"
            mr={{ base: 0, sm: 0, lg: 2, xl: 2 }}
            mt={{ base: 0, sm: 0, lg: 4, xl: 4 }}
            color={"#222"}
            bg="#fafafa"
            icon={<HamburgerIcon w="35px" h="35px" />}
            display={{
              base: "flex",
              sm: "flex",
              md: "none",
              lg: "none",
              xl: "none",
            }}
            onClick={() => setDisplay("flex")}
          />
        </Flex>

        <Flex
          w="100vw"
          zIndex={20}
          h="100vh"
          pos="fixed"
          top="0"
          left="0"
          overflow="auto"
          flexDirection="column"
          display={display}
          bg="white"
        >
          <Flex justify="flex-end">
            <IconButton
              mr={{
                base: 10,
                sm: 10,
                md: 10,
                lg: 10,
                xl: 10,
                "2xl": 20,
                "3xl": 160,
                "4xl": 200,
              }}
              mt={{ base: 4, sm: 4, lg: 4, xl: 4 }}
              color={"black"}
              bg="#fafafa"
              aria-label="Close Menu"
              size="lg"
              icon={<CloseIcon />}
              onClick={() => setDisplay("none")}
            />
          </Flex>
          <Flex flexDir="column" align="center">
            <Link to="#projects">
              <Button
                as="a"
                variant="ghost"
                aria-label="Projects"
                my={5}
                w="100%"
                onClick={() => setDisplay("none")}
              >
                Project
              </Button>
            </Link>
            <Link>
              <Button as="a" variant="ghost" aria-label="Blog" my={5} w="100%">
                Blog
              </Button>
            </Link>
            <Link to="#contact">
              <Button
                as="a"
                variant="ghost"
                aria-label="Contact"
                my={5}
                w="100%"
                onClick={() => setDisplay("none")}
              >
                Contact
              </Button>
            </Link>
          </Flex>
        </Flex>
      </Flex>
    </Box>
  )
}

export default NavBar
